import React, { Component } from 'react';
import { connect } from 'react-redux';
import './index.css';

import * as EvenMoneyActions from '../../actions/evenMoneyActions';

class EvenMoney extends Component {
  componentWillMount() {
    this.props.dispatch(EvenMoneyActions.getHTML());
  }

  render() {
    return (
      <div>
        {this.props.evenmoney.html}
      </div>
    )
  }
}

export default connect((store) => {
  return {
    evenmoney: store.evenmoney,
  }
})(EvenMoney)


