export default function reducer(state = {
  open: false,
  image: null,
  message: '',
}, action) {
  switch (action.type) {
    case 'OPEN_MODAL': {
      return {
        ...state,
        open: true,
        image: action.payload.image,
        message: action.payload.message,
      }
    }
    case 'CLOSE_MODAL': {
      return {
        ...state,
        open: false,
        image: null,
        message: '',
      }
    }
    default: { }
  }
  return state;
}


