import React, { Component } from 'react';
import { connect } from 'react-redux';
import Modal from 'react-modal';
import './index.css';

import * as ModalActions from '../../actions/modalActions';

class ModalHandler extends Component {
  componentWillMount() {
    Modal.setAppElement('body');
  }

  render() {
    return (
      <div>
        <Modal isOpen={this.props.modal.open} style={modalStyle}>
          <div>
            <div className="error-header">
              <img id="error-img" src={this.props.modal.image} alt='Oops!' />
              <h3 className="error-main">Something went wrong...</h3>
            </div><hr />
            <div className="error-text">
              <small className="error-1">{this.props.modal.message}</small>
            </div>
            <button className="btn btn-danger error-ok" onClick={() => this.props.dispatch(ModalActions.closeModal())}>OK</button>
          </div>
        </Modal>
      </div>
    )
  }
}

const modalStyle = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.75)'
  },
  content: {
    top: '33%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    border: '1px solid #ccc',
    background: '#fff',
    overflow: 'auto',
    WebkitOverflowScrolling: 'touch',
    borderRadius: '4px',
    outline: 'none',
    padding: '20px',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  }
}

export default connect((store) => { return { modal: store.modal, } })(ModalHandler);


